import React, { Fragment, useEffect,  useState}  from 'react';
import Moosix from '../functions/mossix-api'
import MusicPreview from "../widgets/musicPreview";
import SliderContainer from "./SliderContainer"
let sliderVal1 = 0;



function Popular(props){
    const app = new Moosix()
    const [popular, setPopular] = useState([]);
 
    useEffect(()=>{
        app.getPopular().then(data=>setPopular(data))
    }, [])

    function play(src){
        props.play(src)
    }

    function stop(){
        props.stop()
    }

    return(
        <SliderContainer name = "popular" h4 = "Popular Musics" context = "Popular Musics For You">
                {popular &&
                    popular.map((name, i) => {
                        return (
                            <Fragment key={i}>
                                <MusicPreview
                                name={name}
                                play={play}
                                userName={props.userName}
                                stop = {stop}
                                audio = {props.audio}
                                index = {0}
                                />
                        </Fragment>
                        );
                })}
        </SliderContainer>

               
            
)

}
export default Popular