import React, { Children,  useState}  from 'react';

let sliderVal2 = 0;
export default function SliderContainer(props){
    const name = props.name
    const [slider2, setSlider2] = useState(sliderVal2)
    return(
        
        <div id={`${name}-container`}   className="home-music-container ">
            <div className="musics-texts">
                <div className="musics-title">
                    <h4>{props.h4}</h4>
                </div>
                <div className="musics-subtext">
                    <span>{props.context}</span>
                </div>
            </div>

            <div className = "line" />
            <div className = "preview-box-scroll">
                <div className="preview-box-container" id = {`${name}-box-container`} style = {{ transform: `translate(${slider2}px, ${0}px)` }}>
                {props.children}
                </div>
            </div>
            
            <div className = "content-slider">
                    <div  id = "next" className = "slider" onClick = {()=>{
                    sliderVal2 -= 225;
                    setSlider2(sliderVal2) 
                    }}>
                        <span className="material-icons">
                            keyboard_arrow_right
                        </span>
                    </div>

                    <div id = "previous" className = "slider" onClick = {()=>{
                        sliderVal2= sliderVal2 + 225;
                        console.log(sliderVal2)
                        setSlider2(sliderVal2) 
                    }}>
                        <span class="material-icons">
                            keyboard_arrow_left
                        </span>
                </div>
                
            </div>

    </div>
    )
}

