import React, { Fragment, useEffect, useState}  from 'react';
import MusicPreview from "../widgets/musicPreview";
import Moosix from '../functions/mossix-api'
import SliderContainer from "./SliderContainer"
let sliderVal2 = 0;



function RecomendFollowing(props){
    const app = new Moosix()
    const [tracks, setTracks] = useState()
    const [slider2, setSlider2] = useState(sliderVal2)
    
    useEffect(() => {
        app.getRecommendByFollowing().then((data)=>setTracks(data))
    }, []);

    function play(src){
        props.play(src)
    }
    function stop(){
        props.stop()
    }

    return(
        <SliderContainer name = "recomend" h4 = "Recommended Musics By Your Followings" context = "Musics for you according to your following">
            {tracks &&
            tracks.map((name, i) => {
                return (
                    <Fragment key={i}>
                        <MusicPreview
                        name={name}
                        play={play}
                        userName={props.userName}
                        stop = {stop}
                        audio = {props.audio}
                        index = {3}
                        />
                    </Fragment>
                );
            })}
        </SliderContainer>
            
    )
} 


export default RecomendFollowing