import React, { Fragment, useEffect,  useState}  from 'react';
import MusicPreview from "../widgets/musicPreview";
import Moosix from '../functions/mossix-api'
import SliderContainer from "./SliderContainer"
let sliderVal2 = 0;


function Trending(props){
    const[trending, setTrending] = useState()
    const [slider2, setSlider2] = useState(sliderVal2)
    const app = new Moosix()
    useEffect(()=>{
      app.getTrending().then(data => setTrending(data))
    }, [])

    function play(src){
        props.play(src)
    }
    function stop(){
        props.stop()
    }

    return(
        
        <SliderContainer name = "trending" h4 = "Trending Musics" context = "Trending Musics For You">
                {trending &&
                    trending.map((name, i) => {
                        return (
                        <Fragment key={i}>
                            <MusicPreview
                            name={name}
                            play={play}
                            stop = {stop}
                            audio = {props.audio}
                            index = {1}
                            />
                        </Fragment>
                    );
                })}
        </SliderContainer>
    )
}

export default Trending