import React, { Fragment, useEffect, useState } from "react";
import firebase from "firebase"
import NavDiv1 from "./nav-comp/navDiv1"
import NavDiv2 from "./nav-comp/navDiv2"
import { useHistory } from 'react-router-dom';
import {MoosaxAuth} from "./functions/moosax-auth.js"
import NavDivMob from "./nav-comp/navDivMob";



function NavDiv(){
    const width = window.innerWidth
    if(width>400){
        return (
            <Fragment>
                <NavDiv1 />
                <NavDiv2 />
            </Fragment>   
        )
    }else{
        return <NavDivMob />
    }
}



function Nav() {
    const width = window.innerWidth
    const history = useHistory()
    const auth = new MoosaxAuth()
    const [uid, setUid] = useState()
    useEffect(()=>{
        auth.getUserId().then((uid)=>{
            setUid(uid)
        })
    }, [])
    const signout = () =>{
        auth.signOut().then((res)=>{
            console.log(res)
            history.push("/login")
        })
    }

    if(width< 500){
        return (
            <div id="nav-mob">
                <nav className = "mob">
                        <div className = "main-nav-mob">
                            <NavDivMob />
                        </div>
                </nav>
            </div>
        )
    }

    else{
        return (
            <div id="nav">
                <nav>
                    <div className = "nav-heading">
                        <h2>Moosax</h2>
                    </div>
                        <div className = "main-nav">
                            <NavDiv1 />
                            <NavDiv2 />
                            <div className = "line" id = "nav-line"/>
                        </div>
                        <div className="signout" onClick={signout}>
                            <span className="material-icons">
                                login
                            </span>
                            <p>signout</p>
                        </div>
                </nav>
            </div>
        )
    }
   
}
export default Nav