import React, {useState, useEffect} from "react";
import { NavLink } from "react-router-dom";


function NavDivMob()
{
    
    return(
            <div className = "nav-div-mob">
                <ul>
                    <NavLink to="/" exact activeClassName="btn-active-mob">
                        <li className="btn">
                            <div className = "nav-btn-container">
                                <span class="material-icons">
                                    home
                                </span>
                            </div>
                           
                        </li>
                    </NavLink>
                    <NavLink to="/explore" exact activeClassName="btn-active-mob">
                        
                            <li className="btn" >
                                <div className = "nav-btn-container"> 
                                    <span class="material-icons">
                                        explore
                                    </span>
                                </div>
                            </li>
                       
                       
                    </NavLink>
                    <NavLink to={"/upload"} exact activeClassName="btn-active-mob">
                            <li className="btn" >
                                <div className = "nav-btn-container"> 
                                    <span class="material-icons">
                                        upload
                                    </span>
                                </div>
                            </li>
                    </NavLink>
                    <NavLink to={"/library"} exact activeClassName="btn-active-mob">
                            <li className="btn" >
                                <div className = "nav-btn-container"> 
                                    <span class="material-icons">
                                        library_music
                                    </span>
                                </div>
                            </li>
                    </NavLink>
                </ul>
            </div>
    )
}

export default NavDivMob