import React, { Fragment, useEffect, useState}  from 'react';
import MusicPreview from "../widgets/musicPreview";
import Moosix from '../functions/mossix-api'
import SliderContainer from "./SliderContainer"
import {uidContext} from "../../context/uid"
import {useContext} from "react"
let sliderVal2 = 0;



function Recomend(props){
    const uid = useContext(uidContext)
    const app = new Moosix()
    const [recomend, setRecomend] = useState()
    const [slider2, setSlider2] = useState(sliderVal2)

    useEffect(() => {
        app.getRecommend(uid).then((data)=>setRecomend(data))
    }, [uid]);


    document.onscroll = function() {
        if(document.documentElement.scrollTop + window.innerHeight == document.documentElement.scrollHeight)
        {
            console.log("scrolled");
        }
    }

    function play(src){
        props.play(src)
    }
    function stop(){
        props.stop()
    }

    return(
        <SliderContainer name = "recomend" h4 = "Recommended musics" context = "Most recomended musics for you">
            {recomend &&
            recomend.map((name, i) => {
                return (
                    <Fragment key={i}>
                        <MusicPreview
                        name={name}
                        play={play}
                        userName={props.userName}
                        stop = {stop}
                        audio = {props.audio}
                        index = {2}
                        />
                </Fragment>
                );
            })}

        </SliderContainer>
    )
} 


export default Recomend